import React from 'react';
import { Switch, Route } from 'react-router-dom';
import Page from '../Page';
import Fade from 'react-reveal/Fade';
import navMap from '../navigation/navMap';

const
  prefix = '/examples/advanced/',
  linkClass = 'nav-link',
  itemClass = 'nav-item',
  nav = navMap({ prefix, linkClass, itemClass }, [
    {title: 'Collapsing Animations', to: 'collapse'},
    {title: 'Test', to: 'test'},
    {title: 'Cascading Effect', to: 'cascade'},
    {title: 'Custom Animations', to: 'custom'},
    {title: 'Stepped Animations', to: 'stepper'},
    {title: 'Form Errors', to: 'forms'},
  ]);

function Advanced() {
  return (
    <div>
      <ul className="nav nav-pills nav-fill mb-3">
        {nav}
      </ul>
      <div className="text-center">
        <Switch>
          <Route render={ ComingSoon }    path={prefix}   exact       />
          <Route render={ Test }    path={prefix + 'test'}  />
          <Route render={ ComingSoon }    path={prefix + 'collapse'}  />
          <Route render={ ComingSoon }    path={prefix + 'cascade'}   />
          <Route render={ ComingSoon }    path={prefix + 'custom'}   />
          <Route render={ ComingSoon }    path={prefix + 'stepper'}   />
          <Route render={ ComingSoon }    path={prefix + 'forms'}   />
          <Route render={ NotFound }                               		/>
        </Switch>
      </div>
    </div>
  );
}

function NotFound() {
  return <Page scroll animate title="Not Found"><h1>Effect not found</h1></Page>
}

function ComingSoon() {
  return (
    <Page scroll title="Coming Soon">
      <h1>Coming Soon</h1>
    </Page>
  );
}

function Test() {
  const min=200, max=800;
  return (
    <Page scroll title="Test">
      <h1>Test</h1>
      {Array(...Array(100)).map( (val, i) =>
        <Fade key={i} duration={2000} left>
          <div>
            <img alt="" src={`https://source.unsplash.com/random/${ Math.floor(Math.random() * (max - min) + min)}x${Math.floor(Math.random() * (max - min) + min)}`} />
            <p>sfsdkhsdjhjkfhkj</p>
            <p>sfsdkhsdjhjkfhkj</p>
            <p>sfsdkhsdjhjkfhkj</p>
            <p>sfsdkhsdjhjkfhkj</p>
            <p>sfsdkhsdjhjkfhkj</p>
            <p>sfsdkhsdjhjkfhkj</p>
            <p>sfsdkhsdjhjkfhkj</p>
          </div>
        </Fade>)}
    </Page>
  );
}


export default Advanced;



